:root {
  --index: calc(1vh + 1vw);
}

.drinkItem {
  position: relative;
  width: 100%;
  max-width: 100px;
  height: 100%;
  padding: calc(var(--index) / 2);
  background-color: white;
  border-radius: 10px;
  cursor: pointer;
}

.drinkImg {
  display: block;
  margin: 0 auto;
  width: 40px;
  height: auto;
}

.drinkTime {
  position: absolute;
  top: 50px;
  left: 8px;
  font-size: 10px;
  color: #686767;
}

@media screen and (max-width: 800px) {
  .drinkTime {
    top: 45px;
  }
}

.drinkName {
  margin-top: 10px;
  text-align: center;
  font-size: 13px;
}

.drinkCount {
  text-align: center;
  color: #686767;
  font-size: 13px;
}
