:root {
  --index: calc(1vh + 1vw);
}

.header {
  position: absolute;
  z-index: 10;
  left: 50%;
  transform: translateX(-50%);
  top: calc(var(--index) * 1);

  color: white;
}

@media screen and (max-width: 370px) {
  .header {
    font-size: 25px;
  }
}

.svg {
  margin-top: calc(var(--index) * -9);
}

@media screen and (max-width: 800px) {
  .svg {
    margin-top: calc(var(--index) * 0.1);
  }
}

.wave {
  animation: wave 32s linear infinite;
}

.wave1 {
  animation: wave1 40s linear infinite;
}

.wave2 {
  animation: wave2 48s linear infinite;
}

@keyframes wave {
  0% {
    transform: translateX(0%);
  }

  100% {
    transform: translateX(100%);
  }
}

@keyframes wave1 {
  0% {
    transform: scaleY(1.2) translateX(0%);
  }

  100% {
    transform: scaleY(1.2) translateX(100%);
  }
}

@keyframes wave2 {
  0% {
    transform: scaleY(0.8) translateX(0%);
  }

  100% {
    transform: scaleY(0.8) translateX(100%);
  }
}
