.formWrapper {
  position: relative;
  width: 100%;
  padding: 10px;
}

.drinks {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(65px, 1fr));
  grid-template-rows: repeat(auto-fit, 80px);
  grid-auto-rows: 80px;
  gap: calc(var(--index));
  width: 100%;
  height: min-content;

  border-radius: 20px;
}

.drinkItem {
  width: 75px;
  height: 80px;
  cursor: pointer;
}

.selected .imgWrapper {
  background-color: #fff;
}

.imgWrapper {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  background-color: #ccebff;
  border-radius: 50%;

  /* transition: all 4.5s ease-in-out; */
}

.img {
  width: 40px;
}

.drinkName {
  color: white;
  text-align: center;
}
