@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;700&family=Roboto:wght@300;400;700&display=swap");

:root {
  --index: calc(1vh + 1vw);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: relative;

  font-family: "Open Sans", sans-serif;
  font-family: "Roboto", sans-serif;
}

svg {
  width: 100%;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

@media screen and (max-width: 800px) {
  .container {
    margin-top: calc(var(--index));
  }
}

.drinksContainer {
  display: flex;
  gap: calc(var(--index) * 5);
  justify-content: center;
  margin: 0 10px;
}
