:root {
  --index: calc(1vh + 1vw);
}

.drinksWrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(65px, 1fr));
  grid-template-rows: repeat(auto-fit, 100px);
  grid-auto-rows: 100px;
  gap: calc(var(--index));
  width: 100%;
  max-height: 280px;
  overflow: auto;
  padding: calc(var(--index));

  background-color: #a3dbff;
  border-radius: 20px;
}

@media screen and (max-width: 500px) {
  .drinksWrapper {
    margin-left: 80px;
  }
}

@media screen and (max-width: 400px) {
  .drinksWrapper {
    max-height: 230px;
  }
}
