:root {
  --index: calc(1vh + 1vw);
  --ratio: 2.24;
  --width: 150px;
}

.bottleContainer {
  position: relative;
  width: var(--width);
}

@media screen and (max-width: 500px) {
  .bottleContainer {
    position: absolute;
    left: 0;
    transform: translateX(-50%);
  }
}

.bottleImg {
  position: absolute;

  width: var(--width);
  height: calc(var(--width) * var(--ratio));
  z-index: 1000;
}

.water {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  width: var(--width);
  height: calc(var(--width) * var(--ratio));
  mask-image: url(../../img/mask.png);
  mask-size: contain;
}

.wave {
  height: 9px;

  background: url(../../img/waveBottle.png);
  background-size: contain;
  background-position: 0px 0px;
  animation: wave 35s linear infinite;
}

.deepWater {
  height: 50%;
  background: #0069ff;
  transition: all 0.5s ease-in-out;
}

@keyframes wave {
  0% {
    background-position: 0px 0px;
  }
  100% {
    background-position: 1000px 0px;
  }
}
