.drinkItem {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;

  /* border: 2px solid #a3dbff; */
  border: 2px solid #ccc;
  border-radius: 50%;
  cursor: pointer;
}

.img {
  display: block;
  margin: 0 auto;
  width: 40px;
  height: auto;
}

.count {
  position: absolute;
  padding: 2px 2px;
  top: -6px;
  border-radius: 10px;
  background-color: white;
  border: 2px solid #ccc;
  font-weight: 300;
  font-size: 12px;
  color: #3b3b3b;
}
