:root {
  --index: calc(1vh + 1vw);
}

.initialize {
  background-color: #1f2028;
  color: #e6e6e6;

  height: 100svh;
  background: url(../../img/stars.jpg) no-repeat;
  background-size: cover;
}

.moon {
  position: absolute;
  width: calc(var(--index) * 6);
  top: calc(var(--index) * 3);
  right: calc(var(--index) * 3);
  transform: matrix(0, 1, 1, 0, 0, 0) rotate(290deg);
}

.title {
  position: absolute;
  top: calc(var(--index) * 8);
  width: max-content;
  left: 50%;
  transform: translateX(-50%);

  font-size: calc(var(--index) * 3.5);
}

@media screen and (max-width: 600px) {
  .title {
    top: calc(var(--index) * 13);
  }
}

.btn {
  display: block;
  position: absolute;
  top: calc(var(--index) * 14);
  width: calc(var(--index) * 9);
  height: calc(var(--index) * 2.8);
  left: 50%;
  transform: translateX(-50%);

  font-size: calc(var(--index) * 1.3);
}

@media screen and (max-width: 600px) {
  .btn {
    top: calc(var(--index) * 25);
    width: calc(var(--index) * 15);
    height: calc(var(--index) * 4.7);
    font-size: calc(var(--index) * 2.3);
  }
}

.waves {
  position: absolute;
  bottom: 0;
  height: 150px;
  width: 100vw;
}

.wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 100px;
  background: url(../../img/wave.png);
  background-size: 1000px 100px;
}

#wave1 {
  opacity: 1;
  bottom: 0;
  z-index: 1000;
  animation: animate 4s linear infinite;
}
#wave2 {
  opacity: 0.5;
  bottom: 10px;
  z-index: 999;
  animation: animate2 4s linear infinite;
}
#wave3 {
  opacity: 0.2;
  bottom: 15px;
  z-index: 998;
  animation: animate 4s linear infinite;
}
#wave4 {
  opacity: 0.7;
  bottom: 20px;
  z-index: 999;
  animation: animate2 4s linear infinite;
}

@keyframes animate {
  0% {
    background-position-x: 1000px;
  }
  100% {
    background-position-x: 0;
  }
}

@keyframes animate2 {
  0% {
    background-position-x: 0px;
  }
  100% {
    background-position-x: 1000px;
  }
}

.ant-modal-footer {
  display: none;
}
